    constructor(props) {
        super(props);
        this.voiceStart = false;
        this.state = {
            enableVoice: false,
            enableInput: false,
            enableEdit: false,
            isMouseMove: false,
            isSentenceParsing: false,
            sentence: '',
            inputValue: '',
            //
            appendSen: "",
            //
            settings: {
                continuous: false, // Don't stop never because i have https connection
                onResult: this.textToInput,
                onStart: function () {
                },
                onEnd: function () {
                }
            }
        };
        //
        // this.UserDictation = artyom.newDictation(this.state.settings);
    }

    
    shouldComponentUpdate(nextProps, nextState){
        if(this.props.question != nextProps.question)
            return true
        else
            return false
    }


    componentDidMount() {
        window.addEventListener("mousemove", () => {
            const { isMouseMove } = this.state;
            !isMouseMove && this.setState({
                isMouseMove: true
            })
        })
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.isClosePannel) {
            this.setState({
                enableVoice: false,
                enableInput: false,
                enableEdit: false,
                sentence: '',
                isSentenceParsing: false
            });
            // console.log(this.voiceStart);
            this.voiceStart = false;
            this.isChrome() && this.UserDictation.stop();
            this.props.closePannel(false)
        }
    }


    componentWillUnmount() {
        window.removeEventListener("mousemove", () => { })
    }
    
    openNotification = placement => {
        notification.info({
            message: 'Parsing Error',
            description:
            'Sorry, we cannot resolve your query at this time. Please input a different query.',
          placement,
        });
    };

    openNotificationModificationError = placement => {
        notification.info({
            message: 'Modification Error',
            description:
            'Sorry, we cannot implement your modifications at this time. Please make other modifications.',
          placement,
        });
    };
    

    question2gif = (question) => {
        if(document.getElementById("pause-btn"))
            document.getElementById("pause-btn").click();

        new Promise(async (resolve, reject) => {
            console.log("question2gif"+question)
            
            let filename = this.props.fileName
            // 5start  end fact

            
            // this.props.isUpdateEditing(true);

            let data = {
                csvname:filename,
                question:question
            }
            // api

            this.props.isGeneratingGIF(true)                                                                                                                                                                                                                                                            

            var response = await api.question2gif(config.url.question2gif,data)
            
            if (response.data.error ==  "Parsing Error" ) {
                console.log('fail')
                this.openNotification('center')
                this.props.isGeneratingGIF(false)
                reject()
                return
            }
            if (response.data.error ==  "Modification Error" ) {
                console.log('fail')
                this.openNotificationModificationError('center')
                this.props.isGeneratingGIF(false)
                reject()
                return
            }

            
            console.log('here .......')
            // reponsenarrative chart
            this.props.isGeneratingGIF(false)
            console.log(response.data)
            this.props.operatorChange(false)
            this.props.animationChange(false)

            this.props.setLeftsiderContent(ActionType.SET_EDIT_SLICE)
            this.props.setPosition(0);
            let percent1 = '0%';
            let div = document.getElementById("timeMarks");
            div.setAttribute('style','background:linear-gradient(90deg,#f28b30 '+percent1+',rgb(246, 246, 247) '+percent1+');height:3px;width:100%;border-radius:15px;margin-top:10px;position: absolute;z-index: 0');
            // this.props.playVideoContinue();
            var tempActions = response.data.actions
            var allCardsTotalTime = tempActions[tempActions.length-1].style.delay+tempActions[tempActions.length-1].style.duration
            this.props.changeAllVideoTime(allCardsTotalTime)
            this.props.changeNowVideoTime(0);

            document.getElementById("play-btn").click();

            this.props.question2Gif(question, response.data)
            this.props.changeTempSpec(response.data)
            

            resolve(response)


        })
    }

    
    // ~~~~~~~~~~~  
